.formulario {
  p {
    font-size: 1.4rem;
    color: $grisOscuro;
    margin: 2rem 0 0 0;
  }

  legend {
    font-size: 2rem;
    color: $grisOscuro;
  }

  label {
    font-weight: $bold;
    text-transform: uppercase;
    display: block;
  }

  input:not([type="submit"]),
  textarea,
  select {
    padding: 1rem;
    display: block;
    width: 100%;
    background-color: $blanco;
    border: 1px solid $gris;
    border-radius: 1rem;
    margin-bottom: 2rem;
  }

  textarea {
    height: 20rem;
  }

  .forma-contacto {
    max-width: 30rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  input[type="radio"] {
    margin: 0;
    width: auto;
  }
}

input {
  border: 2px solid $gris;
  background: none;
  padding: 0;
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  font-weight: 700;
  color: inherit;
  width: 100%;
  height: 4rem;
  border-radius: 0.9rem;
  &:focus {
    outline: none;
    border: 2px solid darken($gris, 5);
  }
}

label {
  display: inline-block;
}
textarea#descripcion {
  height: 10rem;
  width: 100%;
  &:focus {
    outline: none;
    border: 2px solid darken($gris, 5);
  }
}
